import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Tab, Tabs, AppBar, Toolbar } from "@material-ui/core";
import SubscriptionsOutlinedIcon from "@material-ui/icons/SubscriptionsOutlined";
import FeaturedPlayListOutlinedIcon from "@material-ui/icons/FeaturedPlayListOutlined";
import WebOutlinedIcon from "@material-ui/icons/WebOutlined";
import AppsOutlinedIcon from "@material-ui/icons/AppsOutlined";
import ExtensionOutlinedIcon from "@material-ui/icons/ExtensionOutlined";

const useStyles = makeStyles({
  headerRoot: {
    background: "#fff",
    alignItems: "center"
  }
});

export default props => {
  const classes = useStyles();

  return (
    <AppBar
      className={`animated fadeInDown faster ${classes.headerRoot}`}
      position="sticky"
    >
      <Toolbar>
        <Tabs
          value={props.tabIndex}
          onChange={props.handleTabChange}
          indicatorColor="primary"
          textColor="primary"
          centered
        >
          <Tab label="Channels" icon={<SubscriptionsOutlinedIcon />} />
          <Tab label="Playlists" icon={<FeaturedPlayListOutlinedIcon />} />
          <Tab label="Programs" icon={<AppsOutlinedIcon />} />
          <Tab label="Sites" icon={<WebOutlinedIcon />} />
          <Tab label="My VS Code Extensions" icon={<ExtensionOutlinedIcon />} />
        </Tabs>
      </Toolbar>
    </AppBar>
  );
};
